

import { defineComponent } from 'vue';

export default defineComponent({
    name: "SideBar",
    emits: ["changeTheme"],
    data() {
        return {
            darkMode: false
        }
    },
    computed: {
        buttonText() {
            if (this.darkMode) {
                return 'Disable Dark Mode';
            }
            return 'Active Dark Mode';
        }
    },
    methods: {
        changeTheme() {
            this.darkMode = !this.darkMode;
            this.$emit("changeTheme", this.darkMode);
        }
    }
}) 

