

import { defineComponent } from 'vue'

export default defineComponent({
    // eslint-disable-next-line vue/multi-word-component-names
    name: "Button",
    emits: ["clicked"],
    props: {
        disabled: {
            type: Boolean
        },
        icon: {
            type: String,
            required: true
        },
        text: {
            type: String,
            required: true
        }
    },
    methods: {
        clicked(): void {
            this.$emit("clicked")
        }
    }
})

