

import { defineComponent } from 'vue';
import Timer from './Timer.vue';

export default defineComponent({
    name: "FormCard",
    emits: ["whenSaveTask"],
    components: {
        Timer
    },
    data() {
        return {
            description: ""
        }
    },
    methods: {
        timerEnded(elapsedTime: number): void {
            this.$emit("whenSaveTask", {
                secondDuration: elapsedTime,
                description: this.description
            })
            this.description = "";
        }
    }
})

